import {createStore, combineReducers, applyMiddleware, compose} from 'redux';
import createPromiseMiddleware from 'redux-promise-middleware'
import {routerReducer, routerMiddleware} from 'react-router-redux';
import history from '@/History';
import {reducer as homeReducer} from '@/pages/Home';
import {reducer as userCenterReducer} from '@/pages/UserCenter';
// 
const win = window;
const reducer = combineReducers({
  router: routerReducer,
  home: homeReducer,
  userCenter: userCenterReducer
});
const promiseMiddleware = createPromiseMiddleware();

const middlewares = [promiseMiddleware, routerMiddleware(history)];
if (process.env.NODE_ENV !== 'production') {
  // redux-immutable-state-invariantreducer
  middlewares.push(require('redux-immutable-state-invariant').default());
}

const storeEnhancers = compose(
  applyMiddleware(...middlewares),
  (win && win.devToolsExtension) ? win.devToolsExtension() : (f) => f,
);
const store = createStore(reducer, {}, storeEnhancers);
export default store;



