// banner
export const actionHomeGetBanner = () => ({
  type: HOME_GET_BANNER,
  payload: getBanner()
})
// 
export const actionHomeGetNav = () => ({
  type: HOME_GET_NAV,
  payload: getNav()
})
// 
export const actionHomeGetRecommend = () => ({
  type: HOME_GET_RECOMMEND,
  payload: getRecommend()
})



